import React, { PureComponent } from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { LoginWrapper, LoginTitle,LoginBox ,LoginRoot,RegisterTitle ,RegisterWrapper} from './style';
import Button from '../Button/index'
import { actionCreators } from '../store';

class Login extends PureComponent {
	constructor(props){
		super(props);
		this.state={
			account:"",
			password:""
		}
	}
	handleChangeAccount(e){
		let value=e.target.value;
        value=value.replace(/\D/g,'')
		if(value.length>=12){
			value=value.substring(0,11);
		}
        this.setState({
			account:value
		})

	}
	handleChangePwd(e){
		this.setState({
			password:e.target.value
		})
	}
	checkInput(){
		const {account,password}=this.state;
		if(!account||!password){
			alert("");
		}else if(account.length<11){
			alert("11");
		}
		else if(password.length>12){
			alert("12,,");
		}
		else if(password.match(/^(w|d|_)/g)){
			alert(",,");
		}else {
			this.props.login(account,password);
		}

	}
	render() {
		const {loginState}=this.props;
		if (!loginState) {
			return (
			
				<LoginRoot >
					<div className="leftImg">
						<img src="" alt=""></img>
					</div>
				<LoginWrapper>
					<LoginTitle></LoginTitle>
					<form className="login">
						
						<LoginBox style={{marginLeft:"0px"}}>
							<label for="name"></label>
							<input type="text" placeholder='' name="account" onChange={this.handleChangeAccount.bind(this)} value={this.state.account}/>
						</LoginBox>
						<LoginBox style={{marginLeft:"0px"}}>
							<label for="name"></label>
							<input placeholder='' name='password' type="password" onChange={this.handleChangePwd.bind(this)} value={this.state.password}/>
							<i ref={(icon) => {this.showIcon = icon}} className="iconfont show" >&#xe675;</i>
							
						</LoginBox>
						<Button className="primary middle" type="button" style={{right:"20px"}} onClick={this.checkInput.bind(this)}></Button>
						</form>
				</LoginWrapper>
				</LoginRoot>	
			)
		}else {
			return <Redirect to='/index'/>
		}
	}
}

const mapState = (state) => ({
	loginState: state.getIn(['common', 'login']),
   
})

const mapDispatch = (dispatch) => ({
	login(accountElem, passwordElem){
		dispatch(actionCreators.login(accountElem, passwordElem))
	}
    
})


export default connect(mapState, mapDispatch)(Login);